import React from 'react';
import Paginator from '../common/Paginator/Paginator';
import User from './User';
import styles from './Friends.module.css';


const UsersFunc = ({ totalUsersCount, pageSize, currentPage, onPageChanged, users, ...props }) => {
    return <div className={styles.mainContainer}>
        <Paginator itemsTotalCount={totalUsersCount} pageSize={pageSize} currentPage={currentPage} onPageChanged={onPageChanged} />
        <div className={styles.container}>
            {
                users.map(u => <User user={u} key={u.id} followingInProgress={props.followingInProgress} unfollow={props.unFollowThunk} follow={props.followThunk} />)
            }
        </div>
    </div>
}

export default UsersFunc;