import React from 'react';
import ProfileInfo from './ProfileInfo/ProfileInfo';
import Posts from './Posts/Posts';
import styles from './Profile.module.css';


const Profile = ({sendPost, posts, profile, status, ...props}) => {
    return <div className={styles.profile}>
        <ProfileInfo  isMyPage={props.isMyPage} savePhoto={props.savePhotoThunk}
         profile={profile} status={status} updateStatus={props.updateStatusThunk} 
         saveProfileData={props.saveProfileData} isEditMode={props.isEditMode} 
         setEditProfile={props.setEditProfile}/>
        <Posts isMyPage={props.isMyPage}  sendPost={sendPost} posts={posts}  />
    </div>
}

export default Profile;